import React from 'react'
import styled from '@emotion/styled'
import Helmet from 'react-helmet'
import Topbar from '../components/topbar'
import Jumbo from '../components/jumbo'
import Background from '../components/background'
import Footer from "../components/footer"
import BetaBanner from '../components/beta'
import { CoreBox } from '../components/csscomponents'
import withLocation from '../components/withlocation'

const Pagecss = styled.div`
    position: relative;
    margin: 0;
    padding: 0;
    z-index: 1;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
`

const Body = styled.div`
    display: flex;
    flex-wrap: wrap;
    margin-top: 60vh;
    margin: 60vh auto 0 auto;
    // @media (min-width: 1500px){width: 80%;}
    // @media (max-width: 1500px) and (min-width: 1000px){width: 95%;}
    // @media (max-width: 900){width: 98%;}
    width: 95%;
    > div {
        @media (min-width: 1500px){width: 80%;}
        @media (max-width: 1500px) and (min-width: 1000px){width: 95%;}
        @media (max-width: 900){width: 98%;}
        margin: auto;
    }
`
const PagePage = ({search}) => {
    const pageId = search.page !== undefined ? search.page : 'asdf'
    return (
        <Pagecss>
        <Helmet />
        <Topbar />
        <Jumbo />
        <Background />
        <BetaBanner />
        <Body>
            <CoreBox>
                {pageId}
            </CoreBox>
        </Body>
        <Footer />
        </Pagecss>
    )
}

export default withLocation(PagePage)